import { Skill } from "../../shared/contact";

type SkillsProps={
    skills: Skill[]
}
export function SkillsComponent(skillProps:SkillsProps){
    let {skills}= skillProps;
    return(
        <ul>
            {skills.map(skill=><SkillComponent skill={skill} />)}
        </ul>
    )
}

type SkillProps={
    skill: Skill
}

function SkillComponent(skillProps: SkillProps){
    let {skill}= skillProps;
    return(
        <li>
            {skill.title}
        </li>
    )
}