import React from 'react'
import {Experience} from '../../shared/contact'

import './ExperienceStyles.scss'

type ExperiencesProps={
    experiences: Experience[]
}

export let ExperiencesComponent=({experiences}:ExperiencesProps)=>{
    return(
        <ul className="experiences">
            {experiences.map(experience=><ExperienceComponent experience={experience} />)}
        </ul> 
    )

}

type ExperienceProps={
    experience: Experience
}

function ExperienceComponent({experience}:ExperienceProps){
    return(
        <li>
            <div>
                <h4>Empresa:</h4>
                {experience.company}
            </div>
            <div className="functions">
                <h4>Funciones:</h4>
                <ul>
                    {experience.functions.map(task=><li>{task}</li>)}
                </ul>
            </div>

            
        </li>
    )
}
