import React from 'react'
import {AcademicFormation} from '../../shared/contact'

type FormationProps={
    formation: AcademicFormation[]
}

export let FormationComponent=(formationProps:FormationProps)=>{
    let {formation}= formationProps
    return(
        <ul>
            {formation.map(degree=><Degreecomponent degree={degree} />)}
        </ul> 
    )

}

type DegreeProps={
    degree: AcademicFormation
}

function Degreecomponent(degreeProps:DegreeProps){
    let {degree}= degreeProps;
    return(
        <li>
            <h4>{degree.title}</h4>
            <p>{degree.institute}, {degree.year}</p>            
        </li>
    )
}
