import React, { useEffect } from 'react';
import './App.scss';

import { USER } from './shared/user';
import { ExperiencesComponent } from './components/Experience/ExperiencesComponent';
import { SkillsComponent} from './components/Skills/SkillsComponent';
import { FormationComponent } from './components/Formation/FormationComponent';
import { Layout, Row, Col, Avatar, Card, Menu, notification } from 'antd';

import { MailOutlined, LinkedinOutlined, GithubOutlined} from '@ant-design/icons';


const {Header, Content, Footer} = Layout;




function App() {

  const openNotification=(title:string ,msg: string)=>{
    notification.info({
      message: title,
      description: msg,
      placement:"bottomRight",
      duration: 10
    })
  }
  useEffect(()=>{
    openNotification("Página en Construcción","Está Página aún se encuentra en construcción por lo que aun falta información y/o estilos.");
  },[])
  
  return (
    <Layout className="App">
      <Header>
        <Menu mode="horizontal" theme="dark" className="header-menu" defaultSelectedKeys={["1"]}>
          <Menu.Item key="1">Sobre mí</Menu.Item>
          <Menu.Item key="2">Componentes</Menu.Item>
        </Menu>
      </Header>
      <Layout>
        <Content>
          <Row>
            <Col xs={{
              span: 24,
              order: 2
              
            }}
            md={{
              order: 1,
              span: 16
            }}
            >
              <article className="aboutMe">
                <section>
                  <h3>PERFIL PROFESIONAL</h3>
                  <p>{USER.description}</p>
                </section>
                <section>
                  <h3>EXPERIENCIA LABORAL </h3>
                  {USER.experience && <ExperiencesComponent experiences={USER.experience} />}
                </section>

                <section>
                  <h3>FORMACIÓN ACADÉMICA</h3>
                  {USER.academicFormation && <FormationComponent formation={USER.academicFormation} />}
                </section>

                <section>
                  <h3>FORMACIONES COMPLEMENTARIAS</h3>
                  

                </section>

                <section>
                  <h3>CONOCIMIENTOS TÉCNICOS</h3>
                  {USER.skills && <SkillsComponent  skills={USER.skills}/>}

                </section>
              </article>
            </Col>
            <Col xs={{
              span: 24,
              order: 1
            }}
            md={{
              span: 8,
              order: 2
            }}
            >
            <aside>
              <Card className="principal">
                <div className="avatarContainer">
                  <Avatar src={USER.img} size={250} className="avatar"/>
                </div>
                
                <p className="title">{USER.name}</p>
                <p>{USER.career}</p>
                <p><a href={"mailto:"+USER.email}> <MailOutlined /> {USER.email} </a></p>
                <p><a href={USER.linkedin} target="_blank" rel="noreferred"><LinkedinOutlined /> {USER.linkedin}</a> </p>
                <p><a href={USER.github} target="_blank" rel="noreferred"><GithubOutlined /> {USER.github}</a> </p>
                  
              </Card>
            </aside>
            </Col>
          </Row>

        </Content>
        {/* <Sider  >
        </Sider> */}
      </Layout>

      <Footer style={{ textAlign: 'center' }}>Cristian Burbano ©2021 Created by Cristian Burbano</Footer>

    </Layout>
  );
}

export default App;
